import './style.css';
import * as THREE from 'three';
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls.js' 
import * as dat from 'dat.gui'

//debug
const gui = new dat.GUI()
// console.log(OrbitControls);
//textures
const textureLoader = new THREE.TextureLoader()
const cubeTextureLoader = new THREE.CubeTextureLoader()

const doorColorTexture = textureLoader.load('/textures/door/color.jpg')
const doorAlphaTexture = textureLoader.load('/textures/door/alpha.jpg')
const doorAmbientOcclusionTexture = textureLoader.load('/textures/door/ambientOcclusion.jpg')
const doorHeightTexture = textureLoader.load('/textures/door/height.jpg')
const doorNormalTexture = textureLoader.load('/textures/door/normal.jpg')
const doorMetalnessTexture = textureLoader.load('/textures/door/metalness.jpg')
const doorRoughnessTexture = textureLoader.load('/textures/door/roughness.jpg')
const matcapTexture = textureLoader.load('/textures/matcaps/1.png')
const gradientTexture = textureLoader.load('textures/gradients/3.jpg')

const environmentMapTexture = cubeTextureLoader.load(
  [
    '/textures/environmentMaps/0/px.jpg',
    '/textures/environmentMaps/0/nx.jpg',
    '/textures/environmentMaps/0/py.jpg',
    '/textures/environmentMaps/0/ny.jpg',
    '/textures/environmentMaps/0/pz.jpg',
    '/textures/environmentMaps/0/nz.jpg'
  ]
)






gradientTexture.minFilter = THREE.NearestFilter
gradientTexture.magFilter = THREE.NearestFilter
gradientTexture.generateMipmaps = false


//canvas
const canvas = document.querySelector('.webgl');

//sizes
const sizes = {
  width: window.innerWidth,
  height: window.innerHeight
};
//resize
window.addEventListener('resize', ()=>{
  sizes.width = window.innerWidth
  sizes.height = window.innerHeight
  //update camera
  camera.aspect = sizes.width/sizes.height
  camera.updateProjectionMatrix()
  //update renderer
  renderer.setSize(sizes.width, sizes.height); 
  renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

window.addEventListener('dblclick', ()=>{
  if(!document.fullscreenElement){
    canvas.requestFullscreen()
  }else{
    document.exitFullscreen()
  }
})

//scene
const scene = new THREE.Scene();
scene.background = environmentMapTexture
scene.environment = environmentMapTexture
   
//object
// const material = new THREE.MeshBasicMaterial()
// material.map = doorColorTexture;
// material.color = new THREE.Color('pink')
// material.wireframe = true
// material.opacity = 0.5
// material.transparent = true
// material.alphaMap = doorAlphaTexture;
// material.side = THREE.DoubleSide 


// const material =  new THREE.MeshNormalMaterial()
// material.flatShading = true

// const material =  new THREE.MeshMatcapMaterial()
// material.matcap = matcapTexture

// const material = new THREE.MeshDepthMaterial()

// const material = new THREE.MeshLambertMaterial()

// const material = new THREE.MeshPhongMaterial()
// material.shininess = 100 
// material.specular = new THREE.Color(0x1188ff)

// const material = new THREE.MeshToonMaterial()
// material.gradientMap = gradientTexture

const material = new THREE.MeshStandardMaterial()
material.roughness = 0
material.metalness = 1
// material.envMap = enviromentMapTexture
// material.map = doorColorTexture
// material.aoMap = doorAmbientOcclusionTexture
// material.aoMapIntensity = 1
// material.displacementMap = doorHeightTexture
// material.displacementScale = 0
// material.metalnessMap = doorMetalnessTexture
// material.roughnessMap = doorRoughnessTexture
// material.normalMap = doorNormalTexture
// material.normalScale.set(1,1)
// material.transparent = true
// material.alphaMap = doorAlphaTexture

gui.add(material, 'metalness').min(0).max(2).step(0.0001)
gui.add(material, 'roughness').min(0).max(2).step(0.0001)
gui.add(material, 'aoMapIntensity').min(0).max(5).step(0.0001)
gui.add(material, 'displacementScale').min(0).max(1).step(0.0001).name('relief')


const sphere = new THREE.Mesh(
  new THREE.SphereGeometry(0.5, 64, 64),
  material
)
sphere.geometry.setAttribute('uv2', new THREE.BufferAttribute(sphere.geometry.attributes.uv.array, 2))
sphere.position.x = -1.5

const plane = new THREE.Mesh(
  new THREE.PlaneGeometry(1,1,100,100),
  material
)
plane.geometry.setAttribute('uv2', new THREE.BufferAttribute(plane.geometry.attributes.uv.array, 2))

const torus = new THREE.Mesh(
  new THREE.TorusGeometry(0.3,0.2,64,128),
  material
)
torus.position.x = 1.5
scene.add(sphere, plane, torus)
torus.geometry.setAttribute('uv2', new THREE.BufferAttribute(torus.geometry.attributes.uv.array, 2))

// lights
const ambientLight = new THREE.AmbientLight(0xffffff, 0.5)
scene.add(ambientLight)

const pointLight = new THREE.PointLight(0xffffff, 0.5)
pointLight.position.x = 2
pointLight.position.y = 3
pointLight.position.z = 4
scene.add(pointLight)

//camera
const camera = new THREE.PerspectiveCamera(75, sizes.width/sizes.height, 0.1 ,100);
// const aspectRatio = sizes.width/sizes.height
// const camera = new THREE.OrthographicCamera(-1*aspectRatio, 1*aspectRatio, 1, -1, 0.1, 100);
// camera.position.x = 2
// camera.position.y = 2
camera.position.z = 3
console.log(camera.position.length());
// camera.lookAt(cube.position);
scene.add(camera);

//controls
const controls = new OrbitControls(camera, canvas)  
// controls.target.y = 1
// controls.update();
controls.enableDamping = true;

//renderer
const renderer =new THREE.WebGLRenderer(
  {
    canvas : canvas
  }
);
renderer.setSize(sizes.width, sizes.height); 


//animation
const clock = new THREE.Clock()
const loop = () =>
{
  //update objects
  const elapsedTime = clock.getElapsedTime()
  sphere.rotation.y = 0.1 * elapsedTime
  torus.rotation.y = 0.1 * elapsedTime
  plane.rotation.y = 0.1 * elapsedTime

   sphere.rotation.x = 0.1 * elapsedTime
  torus.rotation.x = 0.1 * elapsedTime
  plane.rotation.x = 0.1 * elapsedTime
   
  controls.update()

  renderer.render(scene, camera);
  

  window.requestAnimationFrame(loop); 
}
loop();